import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, zip } from 'rxjs';
import { map } from 'rxjs/operators';
import { AppConstants, LocalStorageConstants } from 'src/app/app.constants';
import { IStyle } from '../models/type';
import { WardrobeStateService } from '../state/wardrobe-state.service';
import { StorageService } from './storage.service';

@Injectable({
  providedIn: 'root',
})
export abstract class WardrobeService {
  constructor(
    private readonly http: HttpClient,
    private readonly storageService: StorageService,
    private readonly wardrobeState: WardrobeStateService
  ) { }

  //TODO: Refacto : put in crud service
  getAllColors(): Observable<any> {
    return this.http.get(AppConstants.colorsUrl);
  }
  getAllColorsAsMap(): Observable<any> {
    return this.http.get(AppConstants.colorsUrlAsMap);
  }
  getAllCategories(): Observable<any> {
    return this.http.get(AppConstants.categoriesUrl);
  }
  getAllSizeGroups(): Observable<any> {
    return this.http.get(AppConstants.sizeGroupsUrl);
  }
  getAllFitsAsMap(): Observable<any> {
    return this.http.get(AppConstants.fitsUrl);
  }
  getAllBrands(): Observable<any> {
    return this.http.get(AppConstants.brandsUrl);
  }
  getMostUsedBrands(): Observable<any> {
    return this.http.get(AppConstants.brandsMostUsedUrl);
  }
  getBrandsAsMap(): Observable<any> {
    return this.http.get(AppConstants.brandsAsMapUrl);
  }
  getCategoriesAsMap(): Observable<any> {
    return this.http.get(AppConstants.categoriesUrlAsMap);
  }

  postNewStyle(style: any): Observable<any> {
    return this.http.post(AppConstants.styleUrl, { style }, { observe: 'response' });
  }

  updateStyle(style: any): Observable<any> {
    return this.http.patch(`${AppConstants.styleUrl}/${style.id}`, style, { observe: 'response' });
  }

  getAllStyles() {
    return this.http.get(`${AppConstants.styleUrl}?include_nested_resources=true`);
  }

  addNewStyle(style: IStyle) {
    this.wardrobeState.addNewStyle(style);
  }
  extractDataFromServer(): Observable<boolean> {
    const colorsResponse = this.getAllColors();
    const colorsMapResponse = this.getAllColorsAsMap();
    const brandsMapResponse = this.getBrandsAsMap();
    const categoriesResponse = this.getAllCategories();
    const brandsResponse = this.getAllBrands();
    const sizeMapResponse = this.getAllSizeGroups();
    const categoriesMapResponse = this.getCategoriesAsMap();
    const mostUsedBrandsResponse = this.getMostUsedBrands();
    const fitsResponse = this.getAllFitsAsMap();
    return zip(
      colorsResponse,
      colorsMapResponse,
      brandsMapResponse,
      sizeMapResponse,
      brandsResponse,
      categoriesResponse,
      mostUsedBrandsResponse,
      categoriesMapResponse,
      fitsResponse
    ).pipe(
      map(plip => {
        plip.forEach((element, index) => {
          this.storageService.storeItemInLocalStorage(
            LocalStorageConstants.STORAGE_KEYS[index],
            JSON.stringify(element.data)
          );
        });
        return true;
      })
    );
  }
}
