import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { LocalStorageConstants } from 'src/app/app.constants';
import { ICategory } from 'src/shared/models/type';
import { StorageService } from 'src/shared/services/storage.service';

@Component({
  selector: 'app-modal-content',
  templateUrl: './modal-content.page.html',
  styleUrls: ['./modal-content.page.scss'],
})
export class ModalContentPage implements OnInit {
  title = 'Catgorie';
  currentCategory: any;
  nextPage = ModalContentPage;
  wardrobeCategories: ICategory[];
  indexes = [];
  ancestors = [];
  wardrobeCategoriesAsMap: any;

  constructor(
    private modalController: ModalController,
    private readonly storageService: StorageService,
    private cdRef: ChangeDetectorRef
  ) { }

  /*ionViewWillEnter() {
    StatusBar.setBackgroundColor({ color: '#FFDE59' })
  }

  ionViewWillLeave() {
    StatusBar.setBackgroundColor({ color: '#FFFFFF' })
  }*/

  ngOnInit() {
    this.wardrobeCategories = this.storageService.getJSONItemFromLocalStorage(
      LocalStorageConstants.CATEGORIES
    );
    this.wardrobeCategoriesAsMap = this.storageService.getJSONItemFromLocalStorage(
      LocalStorageConstants.CATEGORIES_AS_MAP
    );
  }

  goForward(index: number) {
    if (this.wardrobeCategories[index].sub_categories.length) {
      this.indexes.push(this.wardrobeCategories);
      this.ancestors.push(this.wardrobeCategories[index].id);
      this.title = this.wardrobeCategories[index].fr_display;
      this.wardrobeCategories = this.wardrobeCategories[index].sub_categories.length
        ? this.wardrobeCategories[index].sub_categories
        : this.wardrobeCategories;
    }
  }
  goBack() {
    if (this.title === 'Catgorie') {
      this.modalController.dismiss();
    } else {
      this.title =
        this.indexes.length === 1
          ? 'Catgorie'
          : this.wardrobeCategoriesAsMap[this.ancestors[this.indexes.length - 2]].fr_display;
      this.wardrobeCategories = this.indexes[this.indexes.length - 1];
      this.indexes.pop();
      this.ancestors.pop();
    }
  }

  goRoot() {
    //this.nav.popToRoot();
  }

  onChangeCheckbox(item: any) {
    this.currentCategory = item;
    this.modalController.dismiss({
      categoryValue: this.currentCategory,
      indexes: this.indexes,
      ancestors: this.ancestors,
    });
    this.cdRef.detectChanges();
  }
}
