import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { Capacitor } from '@capacitor/core';
import { ActionPerformed, PushNotifications } from '@capacitor/push-notifications';
import { AppConstants, LocalStorageConstants, NotificationsConstants, RoutesConstants } from 'src/app/app.constants';
import { IShopper } from '../models/type';
import { DataService } from './data.service';
import { StorageService } from './storage.service';
import { switchMap, mergeMap, take } from 'rxjs/operators'
@Injectable({
  providedIn: 'root',
})
export abstract class FcmService {
  constructor(
    private readonly http: HttpClient,
    private readonly router: Router,
    private readonly dataService: DataService) {

  }

  initPush(shopperInfos: IShopper) {
    this.http.get(`https://ipapi.co/json`).pipe(take(1), mergeMap((userLocation) => {
      const last_login_location = {
        latitude: (userLocation as any).latitude,
        longitude: (userLocation as any).longitude
      }
      return this.http.patch(`${AppConstants.shopperUrl}/${shopperInfos.id}`, { shopper: { last_login_location } })
    })).subscribe()

    if (Capacitor.getPlatform() !== 'web') {
      this.registerPush(shopperInfos);
    }
  }
  private registerPush(shopperInfos: IShopper) {
    PushNotifications.requestPermissions().then(permission => {
      console.log(permission);
      if (permission.receive === 'granted') {
        PushNotifications.register();
      } else {
        // If permission is not granted
      }
    });
    PushNotifications.addListener('registration', token => {
      console.log(token);
      this.dataService.getData(LocalStorageConstants.DEVICE_TOKEN).then(value => {
        if (token.value !== value) {
          this.dataService.setData(LocalStorageConstants.DEVICE_TOKEN, token.value)
          this.http.patch(
            `${AppConstants.deviceTokenUrl}/${shopperInfos.id}`,
            { device_token: token.value },
            { observe: 'response' }
          ).subscribe(() => console.log("token updated"))
        }
      })
    })
    PushNotifications.addListener('registrationError', err => {
      console.log(err);
    });
    PushNotifications.addListener('pushNotificationReceived', notifications => {
      console.log(notifications);
    });
    PushNotifications.addListener('pushNotificationActionPerformed', notifications => {
      const data = notifications.notification.data;
      switch (data.notification_type) {
        case NotificationsConstants.RECOMMENDATIONS:
          this.router.navigate([RoutesConstants.RECOMMENDATIONS])
          break;
        case NotificationsConstants.RECOMMENDATION_FAILED:
          this.router.navigate([RoutesConstants.RECOMMENDATION_ADVICES, data.categories_to_add_list_string]);
          break;
        default:
          console.log(`Desole mais ce type n'est pas connu ${data.notification_type}.`);
      }
    });
  }
}
