import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class StorageService {
  constructor() { }

  getItemFromSessionStorage(item: string): any {
    return JSON.parse(window.sessionStorage.getItem(item));
  }

  storeItemInSessionStorage(itemName: string, value: string): void {
    window.sessionStorage.setItem(itemName, value);
  }

  getJSONItemFromLocalStorage(item: string): any {
    console.log('item : ' + item);
    return JSON.parse(window.localStorage.getItem(item));
  }
  getItemFromLocalStorage(item: string): any {
    console.log('item : ' + item);
    console.log('plop' + window.localStorage.getItem(item));
    return window.localStorage.getItem(item);
  }
  storeItemInLocalStorage(itemName: string, value: string): void {
    window.localStorage.setItem(itemName, value);
  }
  storeObjectInLocalStorage(itemName: string, value: Object): void {
    window.localStorage.setItem(itemName, JSON.stringify(value));
  }
}
