import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-add-article-title-dialog',
  templateUrl: './add-article-title-dialog.component.html',
  styleUrls: ['./add-article-title-dialog.component.scss'],
})
export class AddArticleTitleDialog implements OnInit {
  title: string;
  isRememberChecked: boolean;
  constructor(private readonly modal: ModalController) {}

  ngOnInit() {}

  onCancel() {
    this.modal.dismiss();
  }
  onSubmitTitle() {
    this.modal.dismiss({ title: this.title, isRemember: this.isRememberChecked });
  }
}
