import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable, of } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { LocalStorageConstants } from 'src/app/app.constants';
import { StorageService } from '../services/storage.service';
import { WardrobeService } from '../services/wardrobe.service';

@Injectable({
  providedIn: 'root',
})
export class StorageResolver implements Resolve<boolean> {
  constructor(
    private readonly wardrobeService: WardrobeService,
    private readonly storageService: StorageService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    console.log('storageresolver');

    //if (!this.hasAllDatasInStorage()) {
    //todo: refacto
    return this.wardrobeService.extractDataFromServer().pipe(
      take(1),
      map(value => value)
    );
    /*} else {
      return of(true);
    }*/
  }

  //TODO: put in sessionstorage service
  private hasAllDatasInStorage(): boolean {
    return (
      LocalStorageConstants.STORAGE_KEYS.filter(element =>
        this.storageService.getJSONItemFromLocalStorage(element)
      ).length === LocalStorageConstants.STORAGE_KEYS.length
    );
  }
}
