import { HttpClient } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { IonButton, ModalController } from '@ionic/angular';
import {
  AngularMyDatePickerDirective,
  IAngularMyDpOptions,
  IMyDateModel,
} from 'angular-mydatepicker';
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { AppConstants, ProfileConstants } from 'src/app/app.constants';
import { IShopper } from 'src/shared/models/type';
import { DataService } from 'src/shared/services/data.service';
import { ToasterService } from 'src/shared/services/toaster.service';
import { AppStateService } from 'src/shared/state/app.state.service';
import { DateFormatter } from 'src/shared/utils/date-formatter.utils';
import { regexValidator } from 'src/shared/validators/regex-validator.directive';

@Component({
  selector: 'app-edit-profile-modal',
  templateUrl: './edit-profile-modal.component.html',
  styleUrls: ['./edit-profile-modal.component.scss'],
})
export class EditProfileModalComponent implements OnInit {
  data: any;
  editForm: FormGroup;
  editInputCtrl: FormControl;
  @ViewChild('checkButton') checkbutton: IonButton;
  hasInputChanged = false;
  subscription: Subscription;
  isTooltipDisplayed = false;

  public disabled = false;

  public myForm: FormGroup;
  genders = ProfileConstants.genders;

  constructor(
    private readonly dataService: DataService,
    private readonly fb: FormBuilder,
    private readonly modal: ModalController,
    private readonly http: HttpClient,
    private toasterService: ToasterService,
    private appState: AppStateService
  ) { }

  ngOnInit() {
    switch (this.data.key) {
      case 'date_of_birth':
        this.editForm = this.fb.group({
          editInputCtrl: [this.data.value, Validators.required]
        });
        this.editForm.valueChanges
          .pipe(debounceTime(100), distinctUntilChanged())
          .subscribe(newValue => {
            this.hasInputChanged = newValue.editInputCtrl !== this.data.value;
            if (!this.hasInputChanged) {
              this.editForm.get('editInputCtrl').setErrors({ inputUnchanged: true })
            }
          });
        break;
      case 'gender_id':
        this.editForm = this.fb.group({
          gender_id: [this.data.value.toString(), Validators.required],
        });
        this.editForm.valueChanges
          .pipe(debounceTime(100), distinctUntilChanged())
          .subscribe(newValue => {
            this.hasInputChanged = newValue.gender_id !== this.data.value.toString();
          });
        break;
      case 'last_name':
        this.editForm = this.fb.group({
          editInputCtrl: [
            this.data.value,
            [Validators.required,
            regexValidator(/^[A-Z-]/, "La premire lettre doit tre une majuscule"),
            regexValidator(/^[a-zA-Z--,_\/.' ]+$/, "Seulement les lettres et ces caractres sont accpts:  - _ / . ' ,"),
            ]
          ],
        });
        this.editForm.valueChanges
          .pipe(debounceTime(100), distinctUntilChanged())
          .subscribe(newValue => {
            this.hasInputChanged = newValue.editInputCtrl !== this.data.value;
            if (!this.hasInputChanged) {
              this.editForm.get('editInputCtrl').setErrors({ inputUnchanged: true })
            }
          });
        break;
      case 'first_name':
        this.editForm = this.fb.group({
          editInputCtrl: [
            this.data.value,
            [Validators.required,
            regexValidator(/^[A-Z-]/, "La premire lettre doit tre une majuscule"),
            regexValidator(/^[a-zA-Z--,_\/.' ]+$/, "Seulement les lettres et ces caractres sont accpts:  - _ / . ' ,"),
            ]
          ],
        });
        this.editForm.valueChanges
          .pipe(debounceTime(100), distinctUntilChanged())
          .subscribe(newValue => {
            this.hasInputChanged = newValue.editInputCtrl !== this.data.value;
            if (!this.hasInputChanged) {
              this.editForm.get('editInputCtrl').setErrors({ inputUnchanged: true })
            }
          });
        break;
      case 'nickname':
        this.editForm = this.fb.group({
          editInputCtrl: [
            this.data.value,
            [Validators.required, Validators.pattern('^[a-z0-9-_]+$')],
          ],
        });
        this.editForm.valueChanges
          .pipe(debounceTime(100), distinctUntilChanged())
          .subscribe(newValue => {
            this.hasInputChanged = newValue.editInputCtrl !== this.data.value;
            if (!this.hasInputChanged) {
              this.editForm.get('editInputCtrl').setErrors({ inputUnchanged: true })
            }
          });
        break;
      default:
        this.editForm = this.fb.group({ editInputCtrl: [this.data.value, Validators.required] });
        this.editForm.valueChanges
          .pipe(debounceTime(100), distinctUntilChanged())
          .subscribe(newValue => {
            this.hasInputChanged = newValue.editInputCtrl !== this.data.value;
            if (!this.hasInputChanged) {
              this.editForm.get('editInputCtrl').setErrors({ inputUnchanged: true })
            }
          });
    }
  }

  onValidate() {
    const shopper = {};
    switch (this.data.key) {
      case 'date_of_birth':
        shopper[this.data.key] = this.editForm.controls['editInputCtrl'].value;
        break;
      case 'gender_id':
        shopper[this.data.key] = this.editForm.controls['gender_id'].value;
        break;
      default:
        shopper[this.data.key] = this.editForm.controls['editInputCtrl'].value.trim();
    }
    this.http
      .patch(
        `${AppConstants.shopperUrl}/${this.data.userID}`,
        { shopper },
        { observe: 'response' }
      )
      .subscribe((response: any) => {
        this.dataService.updateKeyValue("user", response.body.data);
        this.appState.setUserInfos(response.body.data)
        this.toasterService.presentSuccessToast(`MESSAGE.PROFILE.EDIT_${response.status}`);
        this.onDismissModal(response.body.data);
      }),
      error => {
        console.log(error);
        this.toasterService.presentErrorToast('SHARED.ERROR');
      };
  }

  onDismissModal(updatedUser: any = null) {
    this.modal.dismiss(updatedUser);
  }

  extractUSFormattedDateModel(date: IMyDateModel): string {
    return DateFormatter.convertDate(date.singleDate.formatted, 'en');
  }

  ionViewWillDismiss(): void {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  onOpenTooltip() {
    this.isTooltipDisplayed = !this.isTooltipDisplayed;
    if (this.isTooltipDisplayed) {
      document.getElementById('sexe-tooltip').style.visibility = 'visible';
    } else {
      document.getElementById('sexe-tooltip').style.visibility = 'hidden';
    }
  }
}
