import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { LocalStorageConstants } from 'src/app/app.constants';
import { IColor } from 'src/shared/models/wardrobe.model';
import { StorageService } from 'src/shared/services/storage.service';

@Component({
  selector: 'app-colors-select-modal',
  templateUrl: './colors-select-modal.component.html',
  styleUrls: ['./colors-select-modal.component.scss'],
})
export class ColorsSelectModalComponent implements OnInit {
  colors: IColor[];
  selectedColors: IColor[] = [];
  currentCategory: any;
  selectedColorsIds: any;
  constructor(
    private readonly storageService: StorageService,
    private readonly modalController: ModalController
  ) {}

  /*ionViewWillEnter() {
    StatusBar.setBackgroundColor({ color: '#FFDE59' })
  }

  ionViewWillLeave() {
    StatusBar.setBackgroundColor({ color: '#FFFFFF' })
  }*/

  ngOnInit() {
    this.selectedColors = this.currentCategory;
    this.selectedColorsIds = this.selectedColors.map(color => color.id);

    this.colors = this.storageService.getJSONItemFromLocalStorage(LocalStorageConstants.COLORS);
  }

  onChangeCheckbox(item: any) {
    if (this.selectedColors.find(color => color.id === item.id)) {
      this.selectedColors = this.selectedColors.filter(elem => elem.id !== item.id);
    } else {
      this.selectedColors.push(item);
    }
  }

  goBack() {
    this.modalController.dismiss();
  }
  onSave() {
    this.modalController.dismiss(this.selectedColors);
  }
}
