import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { LoadingController } from '@ionic/angular';
import { Observable } from 'rxjs';
import { AppConstants } from 'src/app/app.constants';
import { IShopperHttpResponse } from '../models/http-response.model';
import { IShopper } from '../models/type';

@Injectable({
  providedIn: 'root',
})
export class ShopperCRUDService {
  constructor(
    private readonly http: HttpClient,
    public readonly loadingController: LoadingController
  ) { }

  getShopperInfos(firebaseId: string): Observable<HttpResponse<IShopperHttpResponse>> {
    return this.http.get<IShopperHttpResponse>(`${AppConstants.shopperUrl}/${firebaseId}`, {
      observe: 'response',
    });
  }

  //TODO: change type returned
  createShopper(shopper: IShopper): Observable<any> {
    return this.http.post<any>(AppConstants.shopperUrl, { shopper }, { observe: 'response' });
  }
}
