import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { IShopper } from '../models/type';

@Injectable({
  providedIn: 'root',
})
export class AppStateService {
  private user$: BehaviorSubject<IShopper> = new BehaviorSubject<IShopper>(null);

  constructor() { }

  setUserInfos(user: IShopper): void {
    this.user$.next(user);
  }

  getUserInfosAsObservable(): Observable<IShopper> {
    return this.user$.asObservable();
  }
  getUserInfos(): BehaviorSubject<IShopper> {
    return this.user$;
  }
}