import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { CanLoad, Router } from '@angular/router';
import { FirebaseAnalytics } from '@capacitor-community/firebase-analytics';
import { Observable, of } from 'rxjs';
import { catchError, map, mergeMap } from 'rxjs/operators';
import { ShopperCRUDService } from '../crud/shopper-crud.service';
import { IShopperHttpResponse } from '../models/http-response.model';
import { DataService } from '../services/data.service';
import { FcmService } from '../services/fcm.service';
import { AppStateService } from '../state/app.state.service';


@Injectable({
  providedIn: 'root',
})
export class LoginGuard implements CanLoad {
  constructor(
    private auth: AngularFireAuth,
    private router: Router,
    private shopperCrud: ShopperCRUDService,
    private dataService: DataService,
    private fcmService: FcmService,
    private readonly appState: AppStateService
  ) { }

  canLoad(): Observable<boolean> {
    return this.auth.authState.pipe(mergeMap(state => {
      if (state) {
        FirebaseAnalytics.setUserId({
          userId: state.uid,
        });
        return this.shopperCrud.getShopperInfos(state.uid).pipe(map((response: HttpResponse<IShopperHttpResponse>) => {
          if (state.emailVerified) {
            this.dataService.setData("user", response.body.data);
            this.appState.setUserInfos(response.body.data);
            this.fcmService.initPush(response.body.data);
            return true
          } else {
            this.router.navigate(['/register/4'])
            return false;
          }
        }), catchError(error => {
          console.log(error);
          if (error.status === 404) {
            this.router.navigate(['/register/2']);
          }
          if (error.status === 401) {
            console.log("unauthorized")
            this.router.navigate(['/home'])
          }
          if (error.status === 0) {
            this.router.navigate(['/error']);
          }
          return of(false)
        }))
      } else {
        this.router.navigate(['/home'])
        return of(false)
      }
    }));
  }
}