import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { StorageResolver } from './../shared/resolver/storage.resolver';
import { AngularFireAuthGuard, redirectUnauthorizedTo, redirectLoggedInTo, emailVerified } from '@angular/fire/compat/auth-guard';
import { LoginGuard } from 'src/shared/guards/login.guard';

const redirectUnauthorizedToHome = () => redirectUnauthorizedTo(['/home'])
const redirectLoggedInToMain = () => redirectLoggedInTo(['/main'])

@NgModule({
  imports: [
    RouterModule.forRoot(AppRoutingModule.routes, { preloadingStrategy: PreloadAllModules }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {
  static routes: Routes = [
    {
      path: 'main',
      loadChildren: () => import('./main/main.module').then(m => m.MainPageModule),
      canLoad: [LoginGuard],
      resolve: { StorageResolver },
    },
    {
      path: 'login',
      loadChildren: () => import('./login/login.module').then(m => m.LoginModule),
      canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectLoggedInToMain }
    },
    {
      path: 'register/:slide',
      loadChildren: () => import('./register/register.module').then(m => m.RegisterModule),

    },
    {
      path: 'register-home',
      loadChildren: () => import('./register-home/register-home.module').then(m => m.RegisterHomeModule),
      canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectLoggedInToMain }
    },
    {
      path: 'onboarding',
      loadChildren: () => import('./onboarding/onboarding.module').then(m => m.OnBoardingModule),
      canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectUnauthorizedToHome },
    },
    {
      path: 'onboarding-beyond',
      loadChildren: () => import('./onboarding-beyond/onboarding-beyond.page.module').then(m => m.OnboardingBeyondPageModule),
      canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectUnauthorizedToHome },

    },
    {
      path: 'error',
      loadChildren: () => import('./error/error.module').then(m => m.ErrorModule),
    },
    {
      path: 'recommendation-advice/:advices',
      loadChildren: () => import('./recommendation-advice/recommendation-advice.module').then(m => m.RecommendationAdviceModule),
      canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectUnauthorizedToHome },
    },
    {
      path: 'home',
      loadChildren: () => import('./home/home.module').then(m => m.HomePageModule),
      canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectLoggedInToMain }
    },
    {
      path: 'welcome-features',
      loadChildren: () => import('./welcome-features/welcome-features.module').then(m => m.WelcomeFeaturesModule),
      canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectLoggedInToMain }
    },
    {
      path: '',
      redirectTo: 'main',
      pathMatch: 'full',
    },
  ];
}
