import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { LocalStorageConstants } from 'src/app/app.constants';
import { IArticle } from 'src/shared/models/type';
import { IColor } from 'src/shared/models/wardrobe.model';
import { StorageService } from 'src/shared/services/storage.service';
import { WardrobeStateService } from 'src/shared/state/wardrobe-state.service';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-articles-card-modal',
  templateUrl: './articles-card-modal.component.html',
  styleUrls: ['./articles-card-modal.component.scss'],
})
export class ArticlesCardModalComponent implements OnInit {
  category: string;
  articles$: BehaviorSubject<IArticle[]>;
  colors: IColor[];
  max_items = new Map([
    ['FAM_01', 6],
    ['FAM_02', 1],
    ['FAM_03', 3],
  ]);
  currentArticles: IArticle[];
  selectedArticles: IArticle[];
  constructor(
    private readonly wardrobeState: WardrobeStateService,
    private readonly modal: ModalController,
    private readonly storageService: StorageService
  ) { }

  ngOnInit() {
    this.articles$ = this.wardrobeState.extractArticlesByCategoryId(this.category);
    this.colors = this.storageService.getJSONItemFromLocalStorage(
      LocalStorageConstants.COLORS_AS_MAP
    );
    this.selectedArticles = [...this.currentArticles];
  }

  /*ionViewWillEnter() {
    StatusBar.setBackgroundColor({ color: '#FFDE59' })
  }

  ionViewWillLeave() {
    StatusBar.setBackgroundColor({ color: '#FFFFFF' })
  }*/

  onGoBack() {
    this.modal.dismiss();
  }

  onSelectArticle(index: number, article: IArticle) {
    const htmlElement = document.getElementById('article-card-' + index);
    const articleIndex = this.selectedArticles?.findIndex(elem => elem.id === article.id);
    if (articleIndex === -1 && this.selectedArticles.length !== this.max_items.get(this.category)) {
      htmlElement.style.borderWidth = '3px';
      this.selectedArticles.push(article);
    } else if (articleIndex !== -1) {
      htmlElement.style.borderWidth = '1px';
      this.selectedArticles.splice(articleIndex, 1);
    }
  }

  onConfirmModal() {
    this.modal.dismiss({ articles: this.selectedArticles, category: this.category });
  }

  getClass(article: IArticle) {
    if (this.selectedArticles.length) {
      console.log(this.selectedArticles.includes(article));
      const articlesIds: string[] = this.selectedArticles.map(article => article.id);
      console.log(articlesIds.includes(article.id));
      return articlesIds.includes(article.id) ? 'selected-card' : '';
    }
  }
}
