import { HttpClient } from '@angular/common/http';
import { Component, HostListener, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { AppConstants, LocalStorageConstants } from 'src/app/app.constants';
import { StorageService } from 'src/shared/services/storage.service';

@Component({
  selector: 'app-brands-select-modal',
  templateUrl: './brands-select-modal.component.html',
  styleUrls: ['./brands-select-modal.component.scss'],
})
export class BrandsSelectModalComponent implements OnInit {
  currentBrand: any;
  brands$: any;

  constructor(
    private readonly storageService: StorageService,
    private readonly modalController: ModalController,
    private http: HttpClient
  ) { }

  /*ionViewWillEnter() {
    StatusBar.setBackgroundColor({ color: '#FFDE59' })
  }

  ionViewWillLeave() {
    StatusBar.setBackgroundColor({ color: '#FFFFFF' })
  }*/

  ngOnInit() {
    this.initBrands();
  }

  onConfirmChoice(item: any) {
    this.currentBrand = item;
    this.onSave();
  }

  goBack() {
    this.modalController.dismiss();
  }

  onSave() { }
  private initBrands() {
    this.brands$ = this.storageService.getJSONItemFromLocalStorage(
      LocalStorageConstants.MOST_USED_BRANDS
    );
  }

  onChangeCheckbox(brand) {
    this.currentBrand = brand;
    this.modalController.dismiss(this.currentBrand);
  }

  onSearchChange(event) {
    const input = encodeURIComponent(event.detail.value);

    if (input) {
      this.http
        .get(
          `${AppConstants.brandsUrl}/actions/search?partial_text=${input}&count=20`
        )
        .subscribe((result: any) => (this.brands$ = result.data));
    } else {
      this.initBrands();
    }
  }
}
