import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { LocalStorageConstants } from 'src/app/app.constants';
import { ICategory } from 'src/shared/models/type';
import { IColor, ISize } from 'src/shared/models/wardrobe.model';
import { StorageService } from 'src/shared/services/storage.service';

@Component({
  selector: 'app-sizes-select-modal',
  templateUrl: './sizes-select-modal.component.html',
  styleUrls: ['./sizes-select-modal.component.scss'],
})
export class SizesSelectModalComponent implements OnInit {
  sizes: ISize[];
  currentSize: ISize;
  currentCategory: ICategory;
  constructor(
    private readonly modalController: ModalController
  ) { }

  ngOnInit() {
    this.sizes = this.currentCategory?.wardrobe_additional_fields['size_id'].values;
  }

  onChangeCheckbox(item: any) {
    this.currentSize = item;
  }

  goBack() {
    this.modalController.dismiss();
  }
  onSave() {
    this.modalController.dismiss(this.currentSize);
  }
}
